var base = require('./base');

var regImg = /\.sinaimg\.cn\/[^/]+\/([^.\/]+)(?:\.\w+)?/;

var img2weibo = function img2weibo(url) {
  var matches = url.match(regImg);

  if (!matches) {
    return 0;
  }

  var accountEncoded = matches[1];
  var radix = /^00/.test(accountEncoded) ? 62 : 16;
  return base(accountEncoded, radix);
};

